<template>
  <div>
    <fTitle>
      <template #title>ColorPicker 颜色选择器</template>
      <template #introduce>用于颜色选择，支持多种格式。</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="10"><h3>颜色选择器</h3>
      <div class="grid-content">
      <el-color-picker
      @change="addColor"
  v-model="color"
  show-alpha
  :predefine="predefineColors">
</el-color-picker>
    </div></el-col>
    <el-col :span="10"><h3>最近一次选择颜色</h3><div :style="{background:color}" class="grid-content"></div></el-col>
  </el-row>

    </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      color: 'rgba(255, 69, 0, 0.68)',
      predefineColors: [
        '#ff4500',
        '#ff8c00',
        '#ffd700',
        '#90ee90',
        '#00ced1',
        '#1e90ff',
        '#c71585',
        'rgba(255, 69, 0, 0.68)',
        'rgb(255, 120, 0)',
        'hsv(51, 100, 98)',
        'hsva(120, 40, 94, 0.5)',
        'hsl(181, 100%, 37%)',
        'hsla(209, 100%, 56%, 0.73)',
        '#c7158577'
      ]
    }
  },
  methods: {
    addColor () {
      if (!this.predefineColors.includes(this.color)) { this.predefineColors.push(this.color) }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: 200px;
        height: 200px;
        background: white;
        margin-bottom: 20px;
        display: flex;
  justify-content: center;
  align-items: center;

        }
    }}
</style>
